.app-wrapper {
    display: grid;
    grid-template-rows: 70px 1fr;
    grid-template-columns: 2fr 10fr;
    grid-template-areas:
    " h h "
    " n c ";
    width: 1200px;
    margin: 0 auto;
    grid-gap: 10px;
}

.app-wrapper-content {
    grid-area: c;
    /*background-color: #1698AE;*/
    color: whitesmoke;
}

